// 列表区可以显示所有的物料
// key对应的组件映射关系 
import { ElButton, ElInput } from 'element-plus'
import axios from 'axios';

function createEditorConfig() {
    const componentList = [];
    const componentMap = {}

    return {
        componentList,
        componentMap,
        register: (component) => {
            componentList.push(component);
            componentMap[component.key] = component;
        }
    }
}
export let registerConfig = createEditorConfig();
registerConfig.register({
    label: '这里是文本',
    preview: () => '文本',
    render: () => '渲染文本',
    key: 'text'
})
registerConfig.register({
    label: '按钮',
    preview: () => <ElButton>预览按钮</ElButton>,
    render: () => <ElButton>渲染按钮</ElButton>,
    key: 'button'
})
registerConfig.register({
    label: '输入框',
    preview: () => <ElInput placeholder="预览输入框"></ElInput>,
    render: () => <ElInput placeholder="渲染输入框"></ElInput>,
    key: 'input'
});

// Modify handleDragEnd to send a POST request
registerConfig.handleDragEnd = async () => {
    try {
        // Customize the URL to match your backend endpoint
        const url = 'http://127.0.0.1:5000';
        // Customize the data you want to send
        const data = { message: 'Drag ended, sending message to backend...' };

        // Send a POST request to your backend
        const response = await axios.post(url, data);

        // Handle the response if needed
        console.log('Backend response:', response.data);
    } catch (error) {
        console.error('Error sending POST request:', error);
    }
}